<template>
    <div>
        <van-swipe class="my-swipe" :autoplay="3000">
        <van-swipe-item v-for="(image, index) in images" :key="index">
            <img width="375px" :src="image"/>
        </van-swipe-item>
        </van-swipe>
    </div>
</template>

<script>
export default {
    name: 'Swipe',
    data() {
        return {
            images: []
        };
    },
    created() {
        const images = require('@/api/data/images.json')
        this.images = images.images
    }
}
</script>

<style>
    .my-swipe {
    color: #fff;
    font-size: 20px;
    line-height: 0px;
    text-align: center;
    background-color: #d1d1d1c0;
    }
</style>


