// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'

import { Tag, Icon, Card, Swipe, SwipeItem, Button, Dialog, Popup, Search, Cell, CellGroup, Notify, Image as VanImage, Lazyload, Tabbar, TabbarItem, Collapse, CollapseItem } from 'vant';

Vue.use(Tag);
Vue.use(Icon);
Vue.use(Cell);
Vue.use(Card);
Vue.use(Popup);
Vue.use(Swipe);
Vue.use(Button);
Vue.use(Dialog);
Vue.use(Notify);
Vue.use(Tabbar);
Vue.use(Search);
Vue.use(VanImage);
Vue.use(Lazyload);
Vue.use(Collapse);
Vue.use(SwipeItem);
Vue.use(CellGroup);
Vue.use(TabbarItem);
Vue.use(VueResource);
Vue.use(CollapseItem);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



