<template>
  <div id="app">
    <!-- 轮播 -->
    <Swipe></Swipe>
    
    <!-- 路由 -->
    <router-view/>

    <!-- 标签栏 -->
    <Tabbar></Tabbar>
  </div>
</template>

<script>
  import Swipe from './components/Home/Swipe'
  import Tabbar from './components/Home/Tabbar'

  export default {
    name: 'App',
    components: {
      Swipe,
      Tabbar
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>



