<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <van-button type="primary">主要按钮</van-button>
    <van-button type="info">信息按钮</van-button>
    <van-button type="default">默认按钮</van-button>
    <van-cell value="内容" is-link>
      <!-- 使用 title 插槽来自定义标题 -->
      <template #title>
        <span class="custom-title">单元格</span>
        <van-tag type="danger">标签</van-tag>
      </template>
    </van-cell>

    <van-cell title="单元格" icon="shop-o">
      <!-- 使用 right-icon 插槽来自定义右侧图标 -->
      <template #right-icon>
        <van-icon name="search" class="search-icon" />
      </template>
    </van-cell>

    <van-cell title="底部弹窗" @click="showPopup" is-link />

    <van-cell title="自定义颜色" @click="showNotify" is-link />

    <van-image
      round
      lazy-load
      width="10rem"
      height="10rem"
      src="https://img.yzcdn.cn/vant/cat.jpeg"
    />

    <van-popup 
      v-model="show" 
      closeable
      close-icon="close"
      position="bottom" 
      :style="{ height: '33%' }" 
    >
      <van-image src="https://img.yzcdn.cn/vant/cat.jpeg"></van-image>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      msg: 'Hello World',
      show : false,
    };
  },
  methods:{
    showPopup: function(){
      this.show = true;
    },
    showNotify: function(){
      this.$dialog.confirm({
        title: '标题',
        message: '弹窗内容',
      })
      .then(() => {
        // on confirm
      })
      .catch(() => {
        // on cancel
      });
      this.$notify({
        message: '自定义颜色',
        color: '#ad0000',
        background: '#ffe1e1',
        duration: 1400,
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.custom-title {
  margin-right: 4px;
  vertical-align: middle;
}

.search-icon {
  font-size: 16px;
  line-height: inherit;
}
</style>



